<template lang="html">
  <div class="meeting">


    <b-card  bg-variant="white" text-variant="dark" v-show="true">
        <b-row>
          <b-col sm="8">
            <div class="" align="left">

              <h5>{{ meeting.name}} <b-badge variant="secondary"> : {{meeting.numberOfParticipants}}/{{ meeting.maxParticipants }} </b-badge> </h5>

              <p>
                  <b-badge variant="danger">
                    : {{ category[meeting.categoryId - 1].text }}
                  </b-badge>
                  <b-badge variant="warning">
                    <!-- <b-form-input size="sm" v-model="meeting.startTime"
                            type="date"
                            disabled

                          ></b-form-input> -->
                : {{ meeting.startTime }}
                </b-badge>
            </p>
          </div>
          </b-col>
          <b-col sm="4">
            <div>
              <b-button-group vertical size="sm">
                <b-button variant="outline-secondary" v-on:click="startEditMeeting(index)">  </b-button>
                <b-button variant="outline-secondary" v-on:click="deleteOrUnsubscribe(index)"> /   </b-button>
              </b-button-group>
            </div>
          </b-col>
        </b-row>

        <b-row>
          <b-col>


              <b-btn block @click="showCollapse = !showCollapse"
                  :class="showCollapse ? 'collapsed' : null"
                  aria-controls="collapse4"
                  :aria-expanded="showCollapse ? 'true' : 'false'">
                   
              </b-btn>
              <b-collapse class="mt-2" v-model="showCollapse" id="collapse4">
              <b-card>
                <p>: {{ meeting.description }}</p>
                <p class="card-text">
                  : {{ meeting.meetingAddress.locality}}, {{meeting.meetingAddress.street}}, {{meeting.meetingAddress.house}}
                </p>
                <p>    : {{ meeting.maxParticipants }}</p>
                <p>  : {{ meeting.minAge }}</p>
              </b-card>
              </b-collapse>

          </b-col>
        </b-row>
      </b-card>

      <!-- <b-modal v-model="showEditingMeeting" centered size="lg" title="Large Modal">
        Hello Modal!
      </b-modal> -->

  </div>

</template>

<script>
export default {
  props: ['meeting', 'index'],
  data() {
    return {
      showCollapse: false,
      showEditingMeeting: false,
      category: [
        { value: '1', text: '   ' },
        { value: '2', text: '  ' },
        { value: '3', text: '' },
        { value: '4', text: '  '},
        { value: '5', text: '  '},
        { value: '6', text: ''},
        { value: '7', text: ''},
        { value: '8', text: '  '},
        { value: '9', text: ''},
        { value: '10', text: ''},
        { value: '11', text: ' '},
        { value: '12', text: ''},
        { value: '13', text: ''},
        { value: '14', text: ''},
        { value: '15', text: ''},
        { value: '16', text: ''},
        { value: '17', text: ''},
        { value: '18', text: ''},
        { value: '19', text: ''},
      ]
    }
  },

  methods: {
    meetingChange: function(index){
          this.$emit('meetingchange', index);
      },
    //     
    startEditMeeting: function(index){
      this.$emit('editmeeting', index);
    },

    deleteOrUnsubscribe: function(index){
      this.$emit('deleteorunsubscribe', index);
    }
  }
}
</script>

<style lang="css">
</style>



